.paddle-game-container {
  display: flex;
  justify-content: space-between;
  height: 92vh;
  padding: 10px 23% 2% 2%; 
}

.paddle-game-container h1, h2 {
  color: white;
}

.canvas {
  cursor: none;
  border-radius: 10px;
  left: 100px;
  z-index: 1;
}

.canvas-full-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  border-radius: 0;
}

.btn-full-screen {
  position: fixed;
  top: 65px;
  right: 10px;
  font-size: 20px;
}

.btn-start-stop-reset {
  display: block;
  width: 220px;
  margin-bottom: 10px;
}

.pulse-effect {
  text-transform: uppercase;
  animation: shadow-pulse 1s infinite;
}

@keyframes shadow-pulse {
    0% {
        box-shadow: 0 0 0 0px rgba(255, 255, 255, 0.5);
    }
    100% {
        box-shadow: 0 0 0 10px rgba(255, 255, 255, 0);
    }
}